body {
  font-family: 'Dancing Script', cursive;
  font-family: 'Slackey', cursive;
  font-size: xx-large;
  margin: 0;
}
p {
  font-size: large;
}
.navbar {
  position: fixed;
  top: 0;
  left:0;
  width: 100%;
  height: 15%;
  padding: 0 2rem;
  background: rgba(204, 204, 204, 0.295);
  z-index: 2;
}
.nav-header {
  display: flex;
  justify-content: space-between;
}
.nav-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.nav-links {
  height: 0;
  overflow: hidden;
  transition: var(--mainTransition);
  list-style-type: none;
}
.nav-links a {
  margin: 15px;
  display: block;
  text-decoration: none;
  padding: 1rem 0;
  color: rgb(0, 0, 0);
  transition: var(--mainTransition);
  text-align: center;
  font-size: 1.20rem;
  font-weight: 600;
  font-family: var(--main-font);
  letter-spacing: var(--mainSpacing);
}
.nav-links a:hover {
  opacity: 0.6;
}
.show-nav {
  height: 510px;
}
@media screen and (min-width: 768px) {
  .nav-btn {
    display: none;
  }
  .nav-center {
    margin: 0 auto;
    display: flex;
  }
  .nav-links {
    height: auto;
    display: flex;
    margin-left: 24em;
  }
  .nav-links a {
    margin: 1 1rem;
    padding: 0rem 0;
  }
}
.bottom-left {
  position: absolute;
  bottom: 8px;
  left: 16px;
}
.menu-img {
  position: absolute;
  bottom: -700px;
  right: 16px;
}
.top-right {
  position: absolute;
  top: 8px;
  right: 16px;
}
.bottom-right {
  position: absolute;
  bottom: 8px;
  right: 16px;
}
.centered {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 100%);
}
.bottom-centered {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 70%);
}
